@import "../../styles/variables";
@import "../../styles/media";

.popup {
  width: 100%;
  min-height: 100vh;
  padding: 10px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: $ligthGrey;
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  background: rgba(grey, 0.5); }

.form {
  width: 375px;
  padding: 20px 18px;
  margin-top: 16.5px;
  background-color: $white;
  box-sizing: border-box;
  box-shadow: 0px 1px 0px rgba(90, 97, 105, 0.11), 0px 2px 4px rgba(90, 97, 105, 0.12), 0px 5px 5px rgba(90, 97, 105, 0.06), 0px 3.5px 35px rgba(90, 97, 105, 0.1);
  border-radius: 9px; }

.title {
  font-family: "RobotoReg";
  font-weight: normal;
  font-size: $cell * 4;
  text-align: center;
  margin-bottom: $cell * 4; }

.control {
  display: flex;
  justify-content: center;
  button:first-of-type {
    margin-right: $cell * 2; } }

@include mobileDevice {
  .form {
    width: calc(100% - 5 * #{$cell}); } }
