@import "../../../styles/variables";
@import "../../../styles/media";
@import "../../../styles/mixins";

.button {
  font-size: 11.5px;
  line-height: 13px;
  color: $white;
  width: 110px;
  padding: 8px 0;
  outline: none;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background: $white;
  transition: all $short ease;
  &:disabled {
    color: $mainBlack;
    background: $ultraGrey; } }

.submit {
  background-color: $brightBlue; }

.reset {
  background-color: $crimson; }

.link {
  background-color: $brightBlue;
  display: block;
  text-decoration: none;
  text-align: center;
  width: $cell * 50; }

@include desktopMinDevice {
  .button {
    width: 90px; }
  .link {
    width: $cell * 50; } }

@include mobileDevice {
  .button {
    @include adaptedForMobile(font-size, 10, 14, px);
    width: calc(50% - 5px); }
  .fullWidth {
    width: 100%; }
  .link {
    width: 200px; } }
